import { isServer } from "lit"
import type { ReactiveController, ReactiveControllerHost } from "lit"
import {
  subscribeToDocumentLang,
  subscribeToTranslationRegistration,
  resolveTranslation,
  Translation,
  WellKnownKeys,
  fallback,
} from "./translation.js"

// helper types...

// used to match any function
type Func = (...args: any[]) => any

// if object property is function, use return type, else value type
type Result<Type, Key extends keyof Type> = Type[Key] extends Func ? ReturnType<Type[Key]> : Type[Key]

// if object property is function,
type FuncParams<Type, K extends keyof Type> = Type[K] extends Func ? Parameters<Type[K]> : []

const noop = () => {
  // this function is intentionally left blank
}

export class LocalizeController<TComponentName extends Exclude<keyof Translation, WellKnownKeys>>
  implements ReactiveController
{
  private unsubscribeDocumentLang?: ReturnType<typeof subscribeToDocumentLang>
  private unsubscribeTranslationRegistration?: ReturnType<typeof subscribeToTranslationRegistration>
  private observer?: MutationObserver
  private resolvedTranslation: Translation

  constructor(
    private host: ReactiveControllerHost & HTMLElement,
    private options = { onLangChange: noop }
  ) {
    host.addController(this)
    this.resolvedTranslation = resolveTranslation(this.lang)
  }

  /**
   * The lang of the document or element, with element taking precedence
   */
  get lang() {
    if (isServer) {
      return this.host.lang || "en-US"
    }

    return this.host.lang || document.documentElement.lang
  }

  /**
   * The lang of the translation being applied.
   * This may not match the document/element lang, in case of fallback translation
   */
  get resolvedLang() {
    return this.resolvedTranslation.$lang
  }

  hostConnected() {
    // Always subscribe to translation registration changes (for when new translations are registered)
    this.unsubscribeTranslationRegistration = subscribeToTranslationRegistration(this.handleLangChange)

    // Re-resolve translation now that the host is connected and attributes are set
    // This handles cases where the lang attribute is set after the constructor runs (e.g., in Vue)
    const resolved = resolveTranslation(this.lang)
    if (this.resolvedTranslation !== resolved) {
      this.resolvedTranslation = resolved
    }

    // If the host has its own `lang` attribute, create a local observer to watch for changes to that attribute
    if (this.host.getAttribute("lang")) {
      this.observer = new MutationObserver(this.handleLangChange)
      this.observer.observe(this.host, {
        attributes: true,
        attributeFilter: ["lang"],
      })
    } else {
      // Otherwise, also subscribe to document language changes
      this.unsubscribeDocumentLang = subscribeToDocumentLang(this.handleLangChange)
    }

    this.options.onLangChange()
  }

  hostDisconnected() {
    this.unsubscribeDocumentLang?.()
    this.unsubscribeTranslationRegistration?.()
    this.observer?.disconnect()
  }

  term<Key extends keyof Translation[TComponentName]>(
    key: Key,
    ...args: FuncParams<Translation[TComponentName], Key>
  ): Result<Translation[TComponentName], Key> {
    const { resolvedTranslation, resolvedLang } = this
    const component = this.host.localName as TComponentName

    /* eslint-disable no-console */
    if (resolvedTranslation[component] == null) {
      console.warn(`NORD: Missing translations for component \`${component}\` in lang: \`${resolvedLang}\``)
    } else if (resolvedTranslation[component][key] == null) {
      console.warn(
        `NORD: Missing translation key \`${String(key)}\` for component \`${component}\` in lang \`${resolvedLang}\``
      )
    }
    /* eslint-enable no-console */

    const t = resolvedTranslation[component]?.[key] ?? fallback[component][key]
    return typeof t === "function" ? t(...args) : t
  }

  private handleLangChange = () => {
    const resolved = resolveTranslation(this.lang)

    if (this.resolvedTranslation !== resolved) {
      this.resolvedTranslation = resolved

      this.options.onLangChange()
      this.host.requestUpdate()
    }
  }
}
